body {
  margin: 0;
  padding: 0;
  background: ghostwhite;
  font-family: 'Roboto Condensed', sans-serif;
}

.header {
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-image: url('./utils/img/best_movies.jpg');
  background-position: center;
  position: relative;
  z-index: 1;
}
.header::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: .7;
  z-index: -1;
  background-color: #111;
}

.item {
  margin: 30px 0;
  background-color: #fff;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.5);
          box-shadow: 0 0 10px rgba(0,0,0,0.5);
  padding: 15px 0;
}
.item__img {
  max-width: 300px;
  height: 220px;
}
.item__content {
  font-size: 14px;
  padding:10px;
}
.item__overview {
  margin: 10px 0;
}.item__descr {
  font-size: 18px;
  font-weight: 500;
}
.nav-link {
  cursor: pointer;
  color: #ffffff;
}

.box {
  overflow: hidden;
  height: 70px;
  width: 100%;
  /*line-height: 25px;*/
}

.box:before {
  content: "";
  float: left;
  width: 5px;
  height: 100px;
}

.box > *:first-child {
  float: right;
  width: 100%;
  margin-left: -5px;
}

.box:after {
  content: "\02026";
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  float: right;
  position: relative;
  top: -47px;
  left: 100%;
  width: 3em;
  margin-left: -3em;
  padding-right: 5px;
  text-align: right;
  background-size: 100% 100%;
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, white), to(white));
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), white 50%, white);
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0), white 50%, white);
  background: linear-gradient(to right, rgba(255, 255, 255, 0), white 50%, white);
}

.btn-light {
  margin-right: 15px;
}

.banner {
  background: #fff;
  color: red;
  text-align: center;
}


